<template>
    <div class="animated fadeIn">
        <b-row>
            <b-col>
                <table border="0" class="cTable">
                    <tr class="table-line1">
                        <th>订单id</th>
                        <th>物品</th>
                        <th>订餐人</th>
                        <th>价格</th>
                        <th>电话</th>
                        <th>地址</th>
                    </tr>
                    <tr v-for="(v,k) in list">
                        <td>{{v.id.substring(14)}}</td>
                        <td>{{v.goods[0].name}} 等</td>
                        <td>{{v.name}}</td>
                        <td><del>{{v.ori_price}}</del> / {{v.price}}</td>
                        <td>{{v.phone}}</td>
                        <td>{{v.addr}}</td>
                    </tr>
                </table>
            </b-col>
        </b-row>

    </div>

</template>

<script>
    import normalBtn from './normalBtn.vue';

    export default {
        name: 'tables',
        components: {normalBtn},
        data: () => {
            return {
                list: []
            }
        },

        methods: {
            changeValue(key,value){
                this[key] = value;
            },

            listShop() {
                this.$ajax('order', {
                    method: 'orderList',
                    token: window.localStorage.getItem('token')
                }, (res) => {
                    console.log(res);
                    if (res.code == 0) {
                        this.list = [];
                        for (var i in res.data) {
                            this.list.push(res.data[i]);
                        }
                    } else {
                        alert(res.msg);
                    }
                });
            },

            addShop() {
                console.log(this.addShopData);
                if(confirm("确定要添加\n\n店铺名称："+this.addShopData.name+"\n店铺简介："+this.addShopData.notice+"\n\n吗？")) {
                    this.$ajax('shop',{
                        method: 'addShop',
                        name: this.addShopData.name,
                        notice: this.addShopData.notice,
                        token: window.localStorage.getItem('token')
                    },(res) => {
                        console.log(res);
                        if (res.code == 0) {
                            alert('添加成功');
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    });
                }
            },

            delShop(v) {
                if (confirm("确定要删除以下店铺吗？\n\n" + v.name)) {
                    this.$ajax('shop',{
                        method:'delShop',
                        token: window.localStorage.getItem('token'),
                        shopId: v.id
                    }, (res) => {
                        if (res.code == 0) {
                            alert('删除成功');
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    })
                }
            },

            editShop(id, key) {
                var value = prompt('请输入新的' + (function (key) {
                    switch (key) {
                        case 'name':
                            return '店铺名称';
                        case 'notice':
                            return '店铺简介';
                    }
                })(key));

                if (!value) {
                    return;
                }

                if (confirm('确定要修改吗？')) {
                    this.$ajax('shop', {
                        method: 'editShopInfo',
                        token: window.localStorage.getItem('token'),
                        key: key,
                        value: value,
                        shopId: id
                    }, (res) => {
                        if (res.code == 0) {
                            this.listShop();
                        } else {
                            alert(res.msg);
                        }
                    });
                }
            }
        },

        created() {
            this.listShop();
        }
    }
</script>
