    .cTable {
        width: 100%;
    }

    .cTable * {
        text-align: center;
    }

    .table-line1 {
        border-bottom: solid 2px black;
    }

    .td-btn {
        margin: 10px 10px;
    }

    .menu-btn {
        margin: 10px 0 20px 0;
    }

    .icons {
        display: inline !important;
        font-size: 12px !important;
        margin: 0 5px;
        color: blue;
    }

    .displayAddShopIcon {
        color: white;
    }
</style>